.slide-appear {
    opacity: 0
}

.slide-appear-active {
    opacity: 1;
    -webkit-transition: opacity1000ms;
    transition: opacity1000ms;
}

.slide-enter {
    -webkit-transform: translate(105%, 0%);
            transform: translate(105%, 0%);
}

.slide-enter.slide-enter-active {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    -webkit-transition: -webkit-transform 3000ms ease-out;
    transition: -webkit-transform 3000ms ease-out;
    transition: transform 3000ms ease-out;
    transition: transform 3000ms ease-out, -webkit-transform 3000ms ease-out;
}

/* slide exit */
.slide-exit {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
}

.slide-exit.slide-exit-active {
    -webkit-transform: translateX(-105%, 0);
            transform: translateX(-105%, 0);
    -webkit-transition: -webkit-transform 3000ms ease-out;
    transition: -webkit-transform 3000ms ease-out;
    transition: transform 3000ms ease-out;
    transition: transform 3000ms ease-out, -webkit-transform 3000ms ease-out;
}
.fade-appear {
    opacity: 0
}

.fade-enter-active,
.fade-appear-active {
    opacity: 1;
    -webkit-transition: opacity 1000ms;
    transition: opacity 1000ms;
}

.fade-exit {
    opacity: 1;
}

.fade-exit-active {
    opacity: 0;
    -webkit-transition: opacity 1000ms;
    transition: opacity 1000ms;
}
